

nav.navbar{
  position: fixed;
  width: 100%;
  padding: 0;
  height: 74px;
  transition: height .3s ease-out, background .3s ease-out, box-shadow .3s ease-out;
  transform: translate3d(0, 0, 0);
  z-index: 1003;
  border-bottom: 1px solid rgba(255, 255, 255, .1);

}
nav.sticky{
  height: 60px;
  background: #fff;
}
nav.navbar .navbar-brand {
  color: rgba(255, 255, 255, 0.9);
}
nav.navbar .navbar-brand:hover {
  color: rgba(255, 255, 255, 0.9);
}
nav.navbar .navbar-brand:focus {
  color: rgba(255, 255, 255, 0.9);
}
nav.sticky .container-fluid .navbar-brand {
  color: rgba(2, 2, 2, 0.9);
}
.container-fluid{
  position: relative;
  height: 100%;
}
.navbar-collapse{
  position: absolute;
  float: left;
  right:15%;
  left:15%;
  height: 100%;
}
.navbar-nav{
  height: 100%;
}
nav.navbar .navbar-nav .nav-link{
  height: 100%;
  padding: 0 10px;
}
nav.navbar .navbar-nav .nav-item> a {
  color: rgb(255, 255, 255);
}
nav.navbar .navbar-nav .nav-item> a:hover {
  color: rgb(255, 255, 255);
}
.navbar-nav .nav-item{
  position: relative;
  padding: 1.6rem .625rem 0 .625rem;
  transition: all .3s ease-out;
}
.navbar-nav .nav-item .nav-link{
  position: relative;
  text-transform: uppercase;

}
.sub-menu {
  position: absolute;
  background: #fff;
  border: none;
  border-radius: 2px;
  padding: 0;
  width: 180px;
  top: 105%;
  left: 5%;
  box-shadow: 0 14px 32px 4px rgb(0 0 0 / 30%);
  visibility: hidden;
  opacity: 0;
  transform: translateY(5px);
  transition: opacity .3s .1s, transform .3s .1s, visibility .3s .1s;
}
.sub-menu>li {
  padding: 9px 10px;
  text-align: left;
}
.nav-item:hover>.sub-menu{
  visibility: visible;
  opacity: 1;
  transform: translateY(0px); 
}
.sub-menu>li>a{
  color: #000;
}

.navbar-light .navbar-toggler {
  border: none;
  font-size: 1.125rem;
}
.navbar .navbar-toggler .navbar-toggler-icon {
  background-image: none;
}
.navbar-toggler .navbar-toggler-icon .navbar-toggler-bar {
  display: block;
  position: relative;
  width: 22px;
  height: 1px;
  border-radius: 1px;
  background: #6c757d;
  transition: all .2s;
  margin: 0 auto;
}
.navbar-toggler .navbar-toggler-icon .navbar-toggler-bar.bar2, .navbar-toggler .navbar-toggler-icon .navbar-toggler-bar.bar3 {
  margin-top: 7px;
}
.navbar-toggler[aria-expanded=true] .navbar-toggler-bar.bar2 {
  opacity: 0;
}
.navbar-toggler[aria-expanded=true] .navbar-toggler-bar.bar1 {
  transform: rotate(45deg);
  transform-origin: 10% 10%;
  margin-top: 4px;
}
.navbar-toggler[aria-expanded=true] .navbar-toggler-bar.bar3 {
  transform: rotate(
-45deg
);
  transform-origin: 10% 90%;
  margin-top: 3px;
}
