.module .owl-carousel .owl-nav button.owl-prev{
  position: absolute;
    display: block;
    height: 40px;
    width: 40px;
    top: 50%;
    left:1%;
    margin-top: -20px;
    text-align: center;
    line-height: 40px;
    font-size: 4rem;
    transition: all .2s ease-in-out;
    color: #fff;
}
.module .owl-carousel .owl-nav button.owl-prev:hover{
  background: transparent;
  left:0;
}
.module .owl-carousel .owl-nav button.owl-next {
  position: absolute;
  display: block;
  height: 40px;
  width: 40px;
  top: 50%;
  right: 1%;
  margin-top: -20px;
  text-align: center;
  line-height: 40px;
  font-size: 4rem;
  transition: all .2s ease-in-out;
  color: #fff;
}
.module .owl-carousel .owl-nav button.owl-next:hover{
  background: transparent;
  right:0;
}
.module .owl-theme .owl-dots .owl-dot.active span ,.module .owl-theme .owl-dots .owl-dot:hover span{
  background: #fff;
}


.p-b-40{
