@media (min-width:992px){
  .nav-link::after{
    position: absolute;
    background: rgba(255, 255, 255, .8);
    display: block;
    height: 1px;
    width: 20px;
    bottom: -1px;
    left: 50%;
    margin-left: -10px;
    content: "";
    transition: all .3s ease-out;
  }
  .nav-item:hover>.nav-link::after{
    background: rgba(255, 255, 255, .8);
    margin-left: 0;
    width: 100%;
    left:0;
  }
  
  nav.sticky .navbar-collapse .navbar-nav .nav-item a ,nav.navbar .navbar-nav .nav-item .submenu a{
    color: rgb(5, 5, 5);
  }
  nav.sticky .navbar-collapse .navbar-nav .nav-item a:hover ,nav.navbar .navbar-nav .nav-item .submenu a:hover{
    color: rgb(3, 3, 3);
  }
  nav.sticky .navbar-nav .nav-item{
    padding: 1rem .625rem 0 .625rem;
    transition: all .3s ease-out;
  }
}


@media (max-width:991.98px){
  nav.navbar{
    background: #fff;
  }
  nav.navbar .navbar-brand {
    color: rgba(7, 7, 7, 0.9);
  }
  .navbar-collapse{
    background: rgb(8, 7, 7);
    text-align: left !important;
    max-height: 340px;
    height: auto;
    top: 100%;
    right: 0;
    left: 0;
  }
  .navbar-nav{
    padding: 20px 0;
  }
  nav.navbar .navbar-nav .nav-item{
    padding: 0 7px;
  }
  nav.navbar .navbar-nav .nav-link{
    padding: 10px 0px;
  }
  .sub-menu{
    display: none;
  }
  .main h1{
    font-size: 2rem;
  }
}

@media (max-width:425px){
  .owl-carousel .owl-item .client img{
    width: 44px;
  }
}